@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Overpass:300,400,600,700");
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+JP:100,300,400,500,700,900&subset=japanese");
/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

/*
my form reset
*/
legend {
  width: 0px;
  overflow: hidden;
  padding: 0px;
  position: absolute;
}

fieldset,
button,
option,
select,
textarea,
input[type="button"],
input[type="submit"],
input[type="number"],
input[type="email"],
input[type="tel"],
input[type="text"],
input[type="select"],
input[type="option"],
input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  border: none;
  outline: 0;
  margin: 0;
  background: #fff;
}

/* デフォルトのradio、checkboxは非表示 */
input[type=radio],
input[type=checkbox] {
  display: none;
}

/* for firefox */
select {
  text-indent: 0.01px;
  text-overflow: '';
}

/* for ie10 ie11 ie系のプルダウンの矢印を消す ie9は非対応 */
select::-ms-expand {
  display: none;
}

/* --------------------------------
 * base
 * -------------------------------- */
*, *::before, *::after {
  text-decoration: none;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
  font-size: 62.5%;
}

body {
  width: 100%;
  height: 100%;
}

.global-wrapper {
  overflow: hidden;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

a {
  display: block;
  text-decoration: none;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

.clearfix::after {
  content: '';
  display: block;
  clear: both;
}

.dspNone {
  display: none !important;
}

.hidden {
  visibility: hidden;
}

/* --------------------------------
 * body
 * -------------------------------- */
body {
  background-color: #f0ede5;
  color: #444444;
  font-family: "Noto Sans JP", Overpass, Hiragino Sans, "ヒラギノ角ゴシック", Hiragino Kaku Gothic ProN, "ヒラギノ角ゴ ProN W3", "Avenir", Verdana, YuGothic, "游ゴシック", Roboto, "Droid Sans", Meiryo, "メイリオ", "ＭＳ Ｐゴシック", serif;
  font-size: 1.2rem;
}

/* --------------------------------
 * modifier
 * -------------------------------- */
.pc {
  display: block !important;
}

@media screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}

.sp {
  display: none !important;
}

@media screen and (max-width: 767px) {
  .sp {
    display: block !important;
  }
}

.fixed {
  border-top: 0;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
}

/* --------------------------------
 * layout - common
 * -------------------------------- */
header, footer {
  width: 100%;
}

.home-mv {
  width: 100%;
  margin-bottom: 60px;
}

.breadcrumbs {
  width: 960px;
  margin: 60px auto 30px;
}

.global-wrapper {
  width: 100%;
  padding-bottom: 50px;
}

.global-inner {
  width: 960px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
}

.global-inner main {
  flex: 0 0 640px;
}

.global-inner aside {
  flex: 0 0 280px;
}

.page-inner {
  width: 640px;
  margin: 0 auto;
}

.page-inner main {
  width: 100%;
}

.mainSection-wrapper {
  width: 100%;
  background-color: #f0ede5;
}

.mainSection-inner {
  padding: 30px 15px;
}

/* --------------------------------
 * pagi-navi
 * -------------------------------- */
.wp-pagenavi a, .wp-pagenavi span {
  display: inline-block;
  color: #9E9E9E;
  background-color: #FFFFFF;
  padding: 5px 10px;
  margin: 0 2px;
  white-space: nowrap;
  transition: 0.2s ease-in-out;
  text-align: center;
  text-decoration: none;
  border: 1px solid #b4b7ba;
}

.wp-pagenavi a:hover {
  color: #fff;
  background-color: #2e4a62;
  border: 1px solid #2e4a62;
}

.wp-pagenavi span.current {
  color: #fff;
  font-weight: normal;
  background-color: #9E9E9E;
  border: 1px solid #9E9E9E;
}

.wp-pagenavi .pages {
  border: 0;
}

/* --------------------------------
 * Page transition
 * -------------------------------- */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #EEEEEE;
  /* 背景カラー */
  z-index: 9999;
  /* 一番手前に */
  pointer-events: none;
  /* 他の要素にアクセス可能にするためにポインターイベントは無効に */
  opacity: 0;
  /* 初期値では非表示 */
  transition: opacity .8s ease;
}

body.fadeout::after {
  opacity: 1;
}

/* --------------------------------
 * Top page loading
 * -------------------------------- */
.loading {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  background: #EEEEEE;
  z-index: 9999;
}

.loading-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading-image {
  width: 100px;
  height: 100px;
  background: url("../images/logo01-gold.png") no-repeat;
  background-size: contain;
  margin-bottom: 30px;
  opacity: 0.7;
}

/* アニメーション */
#floatingCirclesG {
  position: relative;
  width: 48px;
  height: 48px;
  margin: auto;
  transform: scale(0.6);
  -o-transform: scale(0.6);
  -ms-transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
}

.f_circleG {
  position: absolute;
  background-color: white;
  height: 9px;
  width: 9px;
  border-radius: 4px;
  -o-border-radius: 4px;
  -ms-border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  animation-name: f_fadeG;
  -o-animation-name: f_fadeG;
  -ms-animation-name: f_fadeG;
  -webkit-animation-name: f_fadeG;
  -moz-animation-name: f_fadeG;
  animation-duration: 1.2s;
  -o-animation-duration: 1.2s;
  -ms-animation-duration: 1.2s;
  -webkit-animation-duration: 1.2s;
  -moz-animation-duration: 1.2s;
  animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
  -ms-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-direction: normal;
  -o-animation-direction: normal;
  -ms-animation-direction: normal;
  -webkit-animation-direction: normal;
  -moz-animation-direction: normal;
}

#frotateG_01 {
  left: 0;
  top: 19px;
  animation-delay: 0.45s;
  -o-animation-delay: 0.45s;
  -ms-animation-delay: 0.45s;
  -webkit-animation-delay: 0.45s;
  -moz-animation-delay: 0.45s;
}

#frotateG_02 {
  left: 6px;
  top: 6px;
  animation-delay: 0.6s;
  -o-animation-delay: 0.6s;
  -ms-animation-delay: 0.6s;
  -webkit-animation-delay: 0.6s;
  -moz-animation-delay: 0.6s;
}

#frotateG_03 {
  left: 19px;
  top: 0;
  animation-delay: 0.75s;
  -o-animation-delay: 0.75s;
  -ms-animation-delay: 0.75s;
  -webkit-animation-delay: 0.75s;
  -moz-animation-delay: 0.75s;
}

#frotateG_04 {
  right: 6px;
  top: 6px;
  animation-delay: 0.9s;
  -o-animation-delay: 0.9s;
  -ms-animation-delay: 0.9s;
  -webkit-animation-delay: 0.9s;
  -moz-animation-delay: 0.9s;
}

#frotateG_05 {
  right: 0;
  top: 19px;
  animation-delay: 1.05s;
  -o-animation-delay: 1.05s;
  -ms-animation-delay: 1.05s;
  -webkit-animation-delay: 1.05s;
  -moz-animation-delay: 1.05s;
}

#frotateG_06 {
  right: 6px;
  bottom: 6px;
  animation-delay: 1.2s;
  -o-animation-delay: 1.2s;
  -ms-animation-delay: 1.2s;
  -webkit-animation-delay: 1.2s;
  -moz-animation-delay: 1.2s;
}

#frotateG_07 {
  left: 19px;
  bottom: 0;
  animation-delay: 1.35s;
  -o-animation-delay: 1.35s;
  -ms-animation-delay: 1.35s;
  -webkit-animation-delay: 1.35s;
  -moz-animation-delay: 1.35s;
}

#frotateG_08 {
  left: 6px;
  bottom: 6px;
  animation-delay: 1.5s;
  -o-animation-delay: 1.5s;
  -ms-animation-delay: 1.5s;
  -webkit-animation-delay: 1.5s;
  -moz-animation-delay: 1.5s;
}

@keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-o-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-ms-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-webkit-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

@-moz-keyframes f_fadeG {
  0% {
    background-color: #8b6f4b;
  }
  100% {
    background-color: white;
  }
}

